export default {
	name: 'goodsItem',
	data () {
		return {
			gidIndex: this.width == 347 ? '2' : this.width == 215 ? '3': 0
		}
	},
	props: {
		obj: {
			type: Object
		},
		width: {
			type: String,
			value: 179
		},
		gid: {
			type: Number
		}
	},
	created() {
		
	},
	computed:{
		rfix () {
				if (this.width == 347){
					if((this.gid + 1) % 2 == 0 ){
						return true
					}else{
						return false
					}
				}else if(this.width == 215) {
					if((this.gid + 1) % 3 == 0){
						return true
					}else{
						return false
					}
				}
			}
	},
	methods:{
		goodsClick(obj){
			this.$emit('goodsClick',obj.goods_id)
		},
		carClick(obj){
			this.$emit('carClick',obj.goods_id)
		}
	}
}
